import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useCreatePoll } from '../api/polls';
import { PollForm } from '../components/PollForm';
import { CreatePollFormData } from '../utils/validators';

export const CreatePoll: React.FC = () => {
  const navigate = useNavigate();
  const createPollMutation = useCreatePoll();

  const handleSubmit = async (data: CreatePollFormData) => {
    try {
      // Transform form data to API format
      const apiData = {
        ...data,
        options: data.options.map(option => option.text)
      };
      const poll = await createPollMutation.mutateAsync(apiData);
      navigate(`/polls/${poll.id}`, { 
        state: { message: 'Poll created successfully!' } 
      });
    } catch (error) {
      console.error('Failed to create poll:', error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Create New Poll</h1>
              <p className="mt-1 text-sm text-gray-600">
                Create a poll to gather opinions from the community
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="bg-white shadow rounded-lg">
          <div className="px-6 py-4 border-b border-gray-200">
            <h2 className="text-lg font-medium text-gray-900">Poll Details</h2>
            <p className="mt-1 text-sm text-gray-600">
              Fill in the details below to create your poll
            </p>
          </div>
          
          <div className="px-6 py-6">
            {createPollMutation.error && (
              <div className="mb-6 bg-red-50 border border-red-200 rounded-md p-4">
                <div className="flex">
                  <div className="flex-shrink-0">
                    <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                    </svg>
                  </div>
                  <div className="ml-3">
                    <h3 className="text-sm font-medium text-red-800">
                      Error creating poll
                    </h3>
                    <div className="mt-2 text-sm text-red-700">
                      <p>Unable to create poll. Please try again.</p>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <PollForm
              onSubmit={handleSubmit}
              isLoading={createPollMutation.isPending}
            />
          </div>
        </div>

        {/* Tips */}
        <div className="mt-8 bg-blue-50 border border-blue-200 rounded-md p-4">
          <div className="flex">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-blue-400" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <h3 className="text-sm font-medium text-blue-800">
                Tips for creating great polls
              </h3>
              <div className="mt-2 text-sm text-blue-700">
                <ul className="list-disc list-inside space-y-1">
                  <li>Make your title clear and specific</li>
                  <li>Provide a detailed description to give context</li>
                  <li>Create balanced, unbiased options</li>
                  <li>Keep options concise but descriptive</li>
                  <li>Consider the audience when writing options</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
