import React from 'react';
import { Link } from 'react-router-dom';
import { Poll } from '../types';

interface PollCardProps {
  poll: Poll;
}

export const PollCard: React.FC<PollCardProps> = ({ poll }) => {
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const getVotePercentage = (option: any) => {
    if (poll.totalVotes === 0) return 0;
    return Math.round((option.votes / poll.totalVotes) * 100);
  };

  return (
    <div className="bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition-shadow">
      <div className="flex justify-between items-start mb-4">
        <h3 className="text-xl font-semibold text-gray-900 line-clamp-2">
          {poll.title}
        </h3>
        <span className="text-sm text-gray-500 ml-4 flex-shrink-0">
          {formatDate(poll.createdAt)}
        </span>
      </div>
      
      <p className="text-gray-600 mb-4 line-clamp-3">
        {poll.description}
      </p>
      
      <div className="mb-4">
        <div className="flex justify-between text-sm text-gray-500 mb-2">
          <span>Total votes: {poll.totalVotes}</span>
          <span>by {poll.authorName}</span>
        </div>
        
        {poll.options.slice(0, 3).map((option) => (
          <div key={option.id} className="mb-2">
            <div className="flex justify-between text-sm mb-1">
              <span className="text-gray-700">{option.text}</span>
              <span className="text-gray-500">{getVotePercentage(option)}%</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${getVotePercentage(option)}%` }}
              />
            </div>
          </div>
        ))}
        
        {poll.options.length > 3 && (
          <p className="text-sm text-gray-500">
            +{poll.options.length - 3} more options
          </p>
        )}
      </div>
      
      <div className="flex justify-between items-center">
        <div className="flex items-center space-x-2">
          {poll.hasVoted && (
            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
               Voted
            </span>
          )}
        </div>
        
        <Link
          to={`/polls/${poll.id}`}
          className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-colors"
        >
          {poll.hasVoted ? 'View Results' : 'Vote Now'}
        </Link>
      </div>
    </div>
  );
};
