import React, { useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { usePoll, useVote, usePollResults } from '../api/polls';
import { VoteOptions } from '../components/VoteOptions';
import { ResultsChart } from '../components/ResultsChart';
import { usePollWebSocket } from '../utils/websocket';
import { PollUpdateMessage } from '../types';

export const PollDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  // const navigate = useNavigate();
  const [isRealTime, setIsRealTime] = useState(false);
  
  const { data: poll, isLoading, error } = usePoll(id!);
  const { data: results, refetch: refetchResults } = usePollResults(id!);
  const voteMutation = useVote();

  // WebSocket for real-time updates
  const handleWebSocketUpdate = (_message: PollUpdateMessage) => {
    setIsRealTime(true);
    // The results will be updated automatically by the query
    refetchResults();
  };

  const { isConnected } = usePollWebSocket(id!, handleWebSocketUpdate);

  const handleVote = async (optionId: string) => {
    if (!id) return;
    
    try {
      await voteMutation.mutateAsync({ pollId: id, data: { optionId } });
      // Results will be refetched automatically
    } catch (error) {
      console.error('Failed to vote:', error);
    }
  };

  const currentPoll = results || poll;

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <svg className="animate-spin h-12 w-12 text-blue-600 mx-auto" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          <p className="mt-4 text-gray-600">Loading poll...</p>
        </div>
      </div>
    );
  }

  if (error || !currentPoll) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.732-.833-2.5 0L4.268 19.5c-.77.833.192 2.5 1.732 2.5z" />
          </svg>
          <h3 className="mt-2 text-sm font-medium text-gray-900">Poll not found</h3>
          <p className="mt-1 text-sm text-gray-500">
            The poll you're looking for doesn't exist or has been removed.
          </p>
          <div className="mt-6">
            <Link
              to="/"
              className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
            >
              Go back to dashboard
            </Link>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center space-x-4">
              <Link
                to="/"
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </Link>
              <div>
                <h1 className="text-3xl font-bold text-gray-900">{currentPoll.title}</h1>
                <p className="mt-1 text-sm text-gray-600">
                  Created by {currentPoll.authorName}  {new Date(currentPoll.createdAt).toLocaleDateString()}
                </p>
              </div>
            </div>
            
            <div className="flex items-center space-x-2">
              {isConnected && (
                <div className="flex items-center space-x-1 text-green-600">
                  <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
                  <span className="text-sm">Live</span>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Poll Description */}
          <div className="bg-white rounded-lg shadow p-6">
            <h2 className="text-lg font-medium text-gray-900 mb-4">Description</h2>
            <p className="text-gray-700 whitespace-pre-wrap">{currentPoll.description}</p>
          </div>

          {/* Poll Stats */}
          <div className="bg-white rounded-lg shadow p-6">
            <h2 className="text-lg font-medium text-gray-900 mb-4">Statistics</h2>
            <div className="space-y-4">
              <div className="flex justify-between">
                <span className="text-gray-600">Total Votes</span>
                <span className="font-semibold">{currentPoll.totalVotes}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Options</span>
                <span className="font-semibold">{currentPoll.options.length}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Created</span>
                <span className="font-semibold">
                  {new Date(currentPoll.createdAt).toLocaleDateString()}
                </span>
              </div>
              {currentPoll.hasVoted && (
                <div className="flex justify-between">
                  <span className="text-gray-600">Your Vote</span>
                  <span className="font-semibold text-green-600"> Voted</span>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Voting Section */}
        {!currentPoll.hasVoted && (
          <div className="mt-8 bg-white rounded-lg shadow p-6">
            <VoteOptions
              poll={currentPoll}
              onVote={handleVote}
              isLoading={voteMutation.isPending}
            />
          </div>
        )}

        {/* Results Section */}
        <div className="mt-8 bg-white rounded-lg shadow p-6">
          <ResultsChart 
            poll={currentPoll} 
            isRealTime={isRealTime}
          />
        </div>

        {/* Actions */}
        <div className="mt-8 flex justify-between items-center">
          <Link
            to="/"
            className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
            Back to Dashboard
          </Link>

          <div className="flex space-x-3">
            <button
              onClick={() => window.location.reload()}
              className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              Refresh
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
