import React from 'react';
import { Poll } from '../types';

interface VoteOptionsProps {
  poll: Poll;
  onVote: (optionId: string) => void;
  isLoading?: boolean;
}

export const VoteOptions: React.FC<VoteOptionsProps> = ({ 
  poll, 
  onVote, 
  isLoading = false 
}) => {
  const handleVote = (optionId: string) => {
    if (!poll.hasVoted && !isLoading) {
      onVote(optionId);
    }
  };

  if (poll.hasVoted) {
    return (
      <div className="bg-green-50 border border-green-200 rounded-md p-4">
        <div className="flex">
          <div className="flex-shrink-0">
            <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
            </svg>
          </div>
          <div className="ml-3">
            <h3 className="text-sm font-medium text-green-800">
              You have already voted!
            </h3>
            <div className="mt-2 text-sm text-green-700">
              <p>You voted for: <strong>{poll.userVote ? poll.options.find(opt => opt.id === poll.userVote)?.text : 'Unknown'}</strong></p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-3">
      <h3 className="text-lg font-medium text-gray-900">Cast your vote:</h3>
      <div className="space-y-2">
        {poll.options.map((option) => (
          <button
            key={option.id}
            onClick={() => handleVote(option.id)}
            disabled={isLoading}
            className="w-full text-left p-4 border border-gray-300 rounded-lg hover:border-blue-500 hover:bg-blue-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
          >
            <div className="flex items-center justify-between">
              <span className="text-gray-900 font-medium">{option.text}</span>
              {isLoading && (
                <svg className="animate-spin h-5 w-5 text-blue-600" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
              )}
            </div>
          </button>
        ))}
      </div>
    </div>
  );
};
